import React, { useEffect, useState } from "react";
import './Signup.scss';

function Signup() {

    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [nick, setNick] = useState('');
    const [email, setEmail] = useState('');
    const [phoneNumber,setPhoneNumber] = useState('');

    // const [gender, setGender] = useState({
    //   none:' ',
    //   female:'',
    //   male:''});
    // const [area, setArea] = useState('');

    const [passwordError,setPasswordError] = useState(false);
    const [passwordLengthError,setPasswordLengthError] = useState(false);

    const [phoneError,setPhoneError] = useState(false);
    const [idError,setIdError] = useState(false);


    // onSubmit      preventDefault 
    const onSubmit = (e) => {
        e.preventDefault();
        if(password.length < 8) {
            return setPasswordLengthError(true);
        }
        if(phoneNumber.length < 13) {
            return setPhoneError(true);
        }
        if(password !== confirmPassword) {
            return setPasswordError(true);
        }
        if(id.length < 4) {
            return setIdError(true);
        }
        console.log({
            id,
            password,
            confirmPassword,
            nick,
            email,
            phoneNumber,
            // area
        });
        // Link  replace   
        window.location.replace("/success")
    };

    const onIdHandler = (e) => {
        const idType = /^[A-Za-z0-9+]{0,15}$/;
        if (idType.test(e.target.value)) {
            setIdError(e.target.value.length <4);
            setId(e.target.value);
        }
    }

    const onPasswordHandler = (e) => {
        setPasswordLengthError(e.target.value < 8);
        setPassword(e.target.value);
    }


    //  
    const onConfirmPasswordHandler = (e) => {
        setPasswordError(e.target.value !== password);
        setConfirmPassword(e.target.value);
    }

    const onNickHandler = (e) => {
        setNick(e.target.value);
    }

    const onEmailHandler = (e) => {
        setEmail(e.target.value);
    }

    const onPhoneHandler = (e) => {
        const phone = /^[0-9\b -]{0,13}$/;
        if (phone.test(e.target.value)) {
            setPhoneError(e.target.value.length < 11);
            setPhoneNumber(e.target.value);
        }
    }

    // const onGenderHandler = (e) => {
    //   setGender(e.target.value)
    // }

    // const onAreaHandler = (e) => {
    //   setArea(e.target.value);
    // }

    useEffect(()=> {
        // phoneNumber  11, 3-4-4 , \d  , {}  , $1    - $2  - $3
        if (phoneNumber.length === 11) {
            setPhoneNumber(phoneNumber.replace(/-/g, '').replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3'));
        }
    },[phoneNumber]);


    return (
        <div className='sign'>
            <h4> </h4>
            <hr/>
            <form onSubmit={onSubmit}>
                {/* // className='signForm'> */}
                <input name="id" type="text" value={id} onChange={onIdHandler} className="idInput" placeholder=""/>
                {idError && <div style={{color : 'red'}}> 4  !!</div>}

                <input name="password" type="password" value={password} onChange={onPasswordHandler} placeholder=""/>
                {passwordLengthError && <div style={{color : 'red'}}> 8  !</div>}
                <input name="confirmPassword" type="password" value={confirmPassword} onChange={onConfirmPasswordHandler} placeholder=" "/>
                {passwordError && <div style={{color : 'red'}}>  !</div>}

                <input name="nick" type="text" value={nick} onChange={onNickHandler} placeholder=''/>
                <input name="email" type="email" value={email} onChange={onEmailHandler} placeholder=''/>
                <input type="text" onChange={onPhoneHandler} value={phoneNumber} placeholder=''/>
                {phoneError && <div style={{color : 'red'}}>  !!</div>}

                {/*  radio */}
                {/* <input name="gender" type="radio" onChange={onGenderHandler}/><h5>{this.useState.none}</h5> */}
                {/* <input name="gender" type="radio" onChange={onGenderHandler}/><h5>{this.useState.female}</h5> */}
                {/* <input name="gender" type="radio" onChange={onGenderHandler}/><h5>{this.useState.male}</h5> */}

                {/*  */}
                {/* <Form.Select>
        <option></option>
        <option value={area} onChange={onAreaHandler}>One</option>
        <option value="aa" onChange={onAreaHandler}>Two</option>
        <option value="cc" onChange={onAreaHandler}>Three</option>
        </Form.Select> */}

                <button type="submit" className="signup_btn"> </button>
            </form>
        </div>
    )
}

export default Signup;